import React, {useState, useEffect, useRef} from 'react';
import styled from 'styled-components';
import axios from 'axios';
import * as config from '../../../config';
import { useNavigate } from 'react-router';
import { refreshToken } from '../../auth/RefreshToken';
import Spinner from 'react-spinkit';
import leftButton from '../../../images/angle-left.svg';
import rightButton from '../../../images/angle-right.svg';
import InputList from './InputList';



const StampIssuance = ({loginCallBack}) => {
  const inputRef = useRef([]);
  const navigate = useNavigate();
  

  const [loading, setLoading] = useState(false);
  const [shopList, setShopList] = useState([]);
  const [shop, setShop] = useState('');
  //const [issuanceDate, setIssuanceDate] = useState(new Date());
  //const [expirationDate, setExpirationDate] = useState('');
  const issuanceDate = new Date();
  const expirationDate = "";
  const [templateList , setTemplateList] = useState([]);
  const [templateIDX, setTemplateIDX] = useState(0);
  const [templateCode, setTemplateCode] = useState();
  const [itemCount, setItemCount] = useState(0);


  useEffect(() => {
    try{
      refreshToken(loginCallBack);
    }catch(e){
      console.log(e);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


//초기 설정한 데이터 가져오기
useEffect(() =>  { 

  async function getTemplate(){
    try {
      await axios.post(`${config.SERVER_URL}/api/get/template`, {}, {
        headers: {
          "Content-Type": `application/json`,
        },
    })
      .then(res => {
        const {shopList , result, templateList} = res.data;

        if(result === "TOKEN ERROR"){
          alert(result);
          navigate("/login")
        }
        if(result === "SUCCESS"){
          setShopList(shopList);
          setShop(shopList[0].SHOP_INFO_NO);       
          setTemplateList(templateList);
          setTemplateCode(templateList[templateIDX].TEMPLATE_CODE);
          setItemCount(templateList[templateIDX].VAR);

        }
        if(result === "TOKEN EXPIRED"){
          alert(result);
          navigate("/login")
        }
          
      })
      .catch(ex => {
      })
      .finally(() => {
        setLoading(true);
      });
    } catch (error) {
      console.log(error);

    } 

   

  }

  getTemplate();
  // eslint-disable-next-line react-hooks/exhaustive-deps
}, [])



useEffect(() =>  { 

  async function getTemplate(){
    try {
      const data ={
        shop_info_no : shop
      }

      await axios.post(`${config.SERVER_URL}/api/get/template`, JSON.stringify(data), {
        headers: {
          "Content-Type": `application/json`,
        },
    })
      .then(res => {
        const {result, templateList} = res.data;

        if(result === "TOKEN ERROR"){
          alert(result);
          navigate("/login")
        }
        if(result === "SUCCESS"){
          setTemplateIDX(0);
          setTemplateList(templateList);

        }
        if(result === "TOKEN EXPIRED"){
          alert(result);
          navigate("/login")
        }
          
      })
      .catch(ex => {
      })
      .finally(() => {
        setLoading(true);
      });
    } catch (error) {
      console.log(error);

    } 

   

  }

  getTemplate();
  // eslint-disable-next-line react-hooks/exhaustive-deps
}, [shop])
   






  function selectShop(e){
    setShop(e.target.value);
    
  }

  function changeTemplate(e){
    const button = e.target.name;

    if(button === "before" && templateIDX !== 0){
      const IDX = templateIDX - 1;
      setTemplateCode(templateList[IDX].TEMPLATE_CODE);
      setTemplateIDX(IDX);
      setItemCount(templateList[IDX].VAR);
    }else if(button === "after" && templateIDX < templateList.length - 1){
      const IDX = templateIDX + 1;
      setTemplateCode(templateList[IDX].TEMPLATE_CODE);
      setTemplateIDX(IDX);
      setItemCount(templateList[IDX].VAR);
    }

  }


  function uploadexcel(e){

    //const template = templateCode;
    const excelval = e.target.value;


   console.log(templateCode);
   if(templateCode == null){
      return;
   }

   var excels = document.querySelectorAll("#uploadExcelForm")[0];
   console.log(excels);

    // if(plusidval !=='admin'){
    //   service = $("#service").val();
    // }else{
    //   service = $("#service_sel").val();
    //   $("#service").val(service);
    // }
   if(excelval !==""){

    const formData = new FormData(excels);
    const fileName = e.target.files[0].name;
    //const files = e.target.files[0];      
    const value = [{
      shop_info_no : shop,
      templateCode : templateCode
    }]

    formData.append("file", excels);
    const blob = new Blob([JSON.stringify(value)], {type : "application/json"})

    formData.append("data" , blob);
       
axios({
  method : "post",
  url : `${config.SERVER_URL}/kakao/uploadExcel.do?template=${templateCode}`,
  data : formData,
  headers: {
    "Content-Type": "multipart/form-data", 
  }
}).then(res => {
  console.log(res);


  const message = res.data.result;
        if(message === "TOKEN ERROR"){
          alert(message);
          navigate("/login")
        }
        if(message === "SUCCESS"){
          alert(message);
          
          const uploadExcel = document.querySelector("#uploadExcel");
          const resultarea = document.querySelector("#resultarea");
          uploadExcel.value = "";
          resultarea.append("\n"+fileName+" : \n" +res.data.comment);


        }
        if(message === "TOKEN EXPIRED"){
          alert(message);
          navigate("/login")
        }
        if(message === "TOKEN NULL"){
          alert(message);
          navigate("/login");
        }
        if(message === "INSERT ERROR"){
          alert('스탬프 발급 오류');
          window.location.reload();

        }
 
})
    
}
}

  function downloadExel(e){
     e.preventDefault();

     var formval = document.uploadExcelForm;
     formval.action = `${config.SERVER_URL}/kakao/downloadExcel?templateCode=${templateCode}`;
     formval.submit();
     
  }

  function 단일업로드(e){

    for(let i = 0; i<inputRef.current.length; i++){
      for(let j = 0; j<itemCount + 1; j++){
        if(inputRef.current[i,j].value === ""){
          alert("입력된 값이 없습니다.")
          inputRef.current[i,j].focus();
          return;
        }
      } 
    }

    return;

      if(templateCode === null){
        return;
      }
		
      const phone_length = document.querySelectorAll(".phonenum").length - 1;
			const excel = document.querySelectorAll("#uploadForm")[0];
      var phonenum = document.querySelector(".phonenum").val();


		// 	if( txtFieldCheck()){

    
          const formData = new FormData(excel);
      
          const value = [{
            shopInfoNo : shop,
            templateCode : templateCode,
            var : itemCount
          }]

          formData.append("file", excel);
          const blob = new Blob([JSON.stringify(value)], {type : "application/json"})
          formData.append("data" , blob);
    

          axios({
            method : "post",
            url : `${config.SERVER_URL}/kakao/uploadOne.do?template=${templateCode}`,
            data : formData,
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }).then(res => {

            console.log(res);


            const message = res.data.result;
                  if(message === "TOKEN ERROR"){
                    alert(message);
                    navigate("/login")
                  }
                  if(message === "SUCCESS"){
                    alert(message);
                    
                    const uploadExcel = document.querySelector("#uploadExcel");
                    const resultarea = document.querySelector("#resultarea");
                    uploadExcel.value = "";
                    resultarea.append("\n"+ phonenum +" : \n" +res.data.comment);
          
          
                  }
                  if(message === "TOKEN EXPIRED"){
                    alert(message);
                    navigate("/login")
                  }
                  if(message === "TOKEN NULL"){
                    alert(message);
                    navigate("/login");
                  }
                  if(message === "INSERT ERROR"){
                    alert('스탬프 발급 오류');
                    window.location.reload();
          
                  }
          })
			
  }

  
  




  if(loading === true){
    return (
        <Wrapper>
            <Form>
               <Title>스탬프  발급 
              <div style={{flex : '1', textAlign : 'right'}}>   
                <Select style={{textAlign : 'center'}} onChange={e => selectShop(e)}>
                  {shopList.map((value, index) => 
                    <option key={value.SHOP_INFO_NO} value={value.SHOP_INFO_NO} >{value.SHOP_NAME}</option>
                    )
                  }
               </Select>
               </div>
               </Title>
           
               <Contents>
               <div className="button_container">
                <div className="image" ><img name="before" alt="before_img" onClick={(e) => changeTemplate(e)} src={leftButton}/></div>
              </div>
               <TemplateContainer>
                    <div className="banner">
                        <div className="speaker"/>
                    </div>
                    
                    <div className="template_title">
                      {templateList.length > 0 ? templateList[templateIDX].TITLE : ""}
                     
                    </div>
                    <div className="templatediv">
                      {
                        <textarea readOnly value={templateList.length > 0 ? templateList[templateIDX].MESSAGE : ""}>
                        </textarea>
                      }
                        
                    </div>
                </TemplateContainer>
                <div className="button_container">
                  <div className="image" ><img name="after" alt="after_img" onClick={(e) => changeTemplate(e)} src={rightButton}/></div>
                </div>
                
                <form id="uploadExcelForm" name="uploadExcelForm" method="post" encType="multipart/form-data" >   
                   <Table>
                     <tbody>
                        <tr>
                          <th className="text_title">엑셀 업로드</th>
                          <td className="text_id">
                            <div id="fileDiv">
                              <input type="file" id="uploadExcel" name="uploadExcel" onChange={(e) => uploadexcel(e)} accept=".xls, .xlsx"/>
                            </div>
                          </td>
                        </tr>
                        <tr>
                          <th>엑셀 다운로드</th>
                          <td>
                            <button onClick={(e) => downloadExel(e)}>엑셀 다운로드</button>
                          </td>
                        </tr>
                        <tr>
                          <th>전송결과</th>
                          <td>
                            <textarea id="resultarea" readOnly>

                            </textarea>
                          </td>
                        </tr>
                      
                     </tbody>
                   </Table>
                   </form>
               </Contents>
              

            </Form>
            <Form>
              <Contents>
              <form id="uploadForm" name="uploadForm" method="post" >
                <table id="upload_info_table" summary="단일 업로드 정보를 입력하는 표">
                  <InputList itemCount={itemCount} inputRef={inputRef}/>                
                </table>
              </form>                
               </Contents>
               <Button onClick={(e) => 단일업로드(e)}>발급</Button>

            </Form>
        </Wrapper>
    );
  }else{
    return (
      <Wrapper>
      <Spinner name="ball-grid-pulse" color="steelblue" />
    </Wrapper>
    )
  }
};

export default StampIssuance;

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
 
  

`
const Form = styled.div`
  border : 0px solid gray; 
  border-radius : 10px;
  box-shadow: 5px 5px 10px 0px gray;
  padding : 40px;
  margin-top : 20px;
`

const Title = styled.div`
  font-size : 30px;
  color : rgba(1, 78, 136, 0.9);
  font-weight: 800;
  width : 100%;
  display: flex;
  flex-direction: row;

 
`

const Contents = styled.div`
  margin-top : 20px;
  display : flex;

  ul {
    list-style:none;
    padding-left : 0;
    margin-left : 0;
  }

  input {
    height : 10px;
    text-align: end;

    padding: 11px 13px;
    background: #f9f9fa;
    color: #000;
    border-radius: 4px;
    outline: 0;
    border: 1px solid rgba(245, 245, 245, 0.7);
    font-size: 15px;
    transition: all 0.3s ease-out;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.1), 0 1px 1px rgba(0, 0, 0, 0.1);
    :focus,
    :hover {
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.15), 0 1px 5px rgba(0, 0, 0, 0.1);
    }
  }

  #uploadExcel {
    height : 100%;
    border : 0px;
    box-shadow : 0 ;
  }

  .button_container {
    height : 400px;
    width : 30px;
    display : table;
    padding : 5px;
  }
  .image {
    vertical-align: middle;
    width : 20px;
    height : 40px;
    display : table-cell;

  }

  img {
    width : 100%;
    cursor : pointer;
    -webkit-font-smoothing: antialiased;
    

    :hover {
      filter: invert(50%);
    }
  }

`

const Table = styled.table`
  border: 0px;
  border-collapse: separate;
  border-spacing: 0 10px;

  tbody th {
    font-weight : 400;
    padding : 10px;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type=number] {
    -moz-appearance: textfield;
  }

  textarea {
    width : 100%;
    height : 300px;
  }
`

const Button = styled.button`
  margin-top : 10px;
  width : 100%;
  height : 50px;
  margin-left : 5px;
  margin-right : 5px;
  height : 25px;
  align-self: flex-end;

  border-radius: 4px;
  font-size : 15px;
  outline: 0;
  border: 0px solid rgba(1, 78, 136, 0.9);
  background-color : rgba(1, 78, 136, 0.9);
  color : rgba(255,255,255);

  &:hover {
    cursor:pointer;
  }
`


const Select = styled.select`
  height : 30px;
  background: #f9f9fa;
  border-radius: 4px;
  color: #000;
  outline: 0;
  border: 1px solid rgba(245, 245, 245, 0.7);
  font-size: 15px;
  transition: all 0.3s ease-out;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1), 0 1px 1px rgba(0, 0, 0, 0.1);
  :focus,
  :hover {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.15), 0 1px 5px rgba(0, 0, 0, 0.1);
  }
  font-family: inherit;
`


const TemplateContainer = styled.div`
        width: 220px;
        height: 400px;
        border-radius: 20px;
        border: 7px solid #343434;
        background-color: #fff;
        float: left;    

        
        .banner {
            width: 80px;
            height: 20px;
            border-radius: 0 0 15px 15px;
            background-color: #343434;
            margin: 0px auto;
            margin-bottom : 15px;

            .speaker{
                width: 40px;
                height: 3px;
                border-radius: 0 0 3px 3px;
                background-color: #454545;
                margin: -4px auto;
            }
        }

        .templatediv{
            margin: 0 auto;
            text-align: center;

            textarea {
                width: 205px;
                height: 330px;
                background-color: #fff38e;
                border: 0;
                font-size : 12px;

            }
        }

        .template_title { 
          width : 100%;
          height : 20px;
          text-align : center;
          margin-bottom : 5px;
        }


`

